import $ from 'jquery';

window.addEventListener('scroll', function(){
    const navigasi = document.querySelector('#navigasi');
    navigasi.classList.toggle('shadow', window.scrollY > 0);
    navigasi.classList.toggle('p-2', window.scrollY > 0);
})

$('.kategori-portofolio').on('click',function() {
    $('.kategori-portofolio').removeClass('active-portofolio');
    $(this).addClass('active-portofolio');
})
